<template>
  <div class="about-us-div">
    <section class="blog-details spad">
      <div class="container">
        <div class="row d-flex justify-content-center">
          <div class="col-lg-8">
            <div class="blog__details__title">
              <h6>Last update date <span>- Nov 25th, 2021</span></h6>
              <h2>For Authentic Movie Database(AMDB) Beginners:</h2>
              <h6>Customised Multi-Sourced Movie Guide</h6>
            </div>
          </div>
          <div class="col-lg-8">
            <div class="blog__details__content">
              <div class="blog__details__text">
                <p>
                  AMDb (Authentic Movie Database) is a movie review platform
                  with a movies recommender system and an authentic comment
                  detector which could filter toxic comments and analyze the
                  sentiment of movie reviews. All the users can view or share
                  some related movie comments in a fair and authentic
                  environment by using AMDb.
                </p>
              </div>
              <div class="blog__details__item__text">
                <h4>Comment detector:</h4>
                <p>
                  For AMDb, when users post their comment on AMDb platform, it
                  will be reminded this comments toxic rate and sentiment
                  result to users. Users can filter comments based on their
                  tolerance for the toxic level or emotional bias. In this way,
                  a relatively non-toxic and fair communication environment can
                  be created for users.
                  <br />
                  For other platforms, AMDb supports browsing of film reviews
                  from TMDb, IMDb, YouTube and Twitter. At the same time, each
                  film review has been tested by the toxic comment detector and
                  its corresponding results are attached in the end of the
                  comment box.
                </p>
              </div>
              <div class="blog__details__item__text">
                <h4>Toxic Level Icons and Explanation</h4>
                <div class="M" style="">
                  <div class="t">
                    <img
                      src="@/assets/toxic-green.png"
                      alt="toxic level 1"
                      style="height: 10%"
                    />
                  </div>
                  <div class="t">
                    <img
                      src="../assets/toxic-yellow.png"
                      alt="toxic level 2"
                      style="height: 10%"
                    />
                  </div>
                  <div class="t">
                    <img
                      src="../assets/toxic-red.png"
                      alt="toxic level 3"
                      style="height: 10%"
                    />
                  </div>
                </div>
                <div class="p-conent">
                  <p>
                    When the toxic rate is within the range of 0-53%, the system
                    will use a green grunt head to indicate that it is
                    non-toxic. Green symbolizes non-toxicity. When the toxic
                    rate is within the range of 54-90%, the system will use a
                    yellow grunt to indicate that it is toxic. When the toxic
                    rate exceeds 90%, the red grunt head is used by the system
                    to indicate that it is sever toxic
                  </p>
                </div>
              </div>
              <div class="blog__details__item__text">
                <h4>sentiment Level Icons and Explanation</h4>
                <div class="M" style="">
                  <div class="t">
                    <img
                      src="../assets/sentiment-green.png"
                      alt="Positive"
                      style="height: 10%"
                    />
                  </div>
                  <div class="t">
                    <img
                      src="../assets/sentiment-red.png"
                      alt="Negative"
                      style="height: 10%"
                    />
                  </div>
                </div>
                <div class="p-conent">
                  <p>
                    After the result of the detector detection, if it is less
                    than 50%, it is judged as negative by the system and the red
                    angry face icon is adopted; if it is higher than 50%, it is
                    regarded as positive and the green smiling face icon is
                    adopted.
                  </p>
                </div>
              </div>
            </div>
          </div>

          <div class="col-lg-8">
            <div class="blog__details__item__text">
              <h4>The worst movies</h4>
              <p>
                Why we have worst rated movie informations in our system?
                <br />
                In our website we have a part where we are getting worst rated
                movies from Imdb, there are two reasons why we are doing that.
                First Reason is people are generally interested in best rated
                movies, but they are also curious about worst rated movies and
                most people find that interesting. Another reason to have worst
                rated movies in our system is, it is a great way to represent
                capabilities of our toxicity analyses and sentimental analyses
                since worst rated movies generally include harsh and negative
                reviews.
              </p>
            </div>
            <!-- IMDB bot 10 movies  -->
            <div class="trending__product" id="imdbbot">
              <div class="row">
                <div class="col-lg-8 col-md-8 col-sm-8">
                  <!-- <div class="section-title">
                    <h4>The Worst Movies</h4>
                  </div> -->
                </div>
                <div class="col-lg-4 col-md-4 col-sm-4">
                  <div class="btn__all">
                    <a
                      id="ImdbBotMore"
                      @click="showResultPage()"
                      class="primary-btn"
                      >View All <span class="arrow_right"></span
                    ></a>
                  </div>
                </div>
              </div>
              <div class="row" v-if="imdbBotMovies.length > 0">
                <div
                  class="col-lg-4 col-md-6 col-sm-6"
                  v-for="item in imdbBotMovies"
                  :key="item.id"
                >
                  <router-link :to="'/movie/' + item.tmdb_Id">
                    <div class="product__item">
                      <div
                        v-bind:style="{
                          'background-image': 'url(' + item.posters[0] + ')',
                        }"
                        class="product__item__pic set-bg testmain"
                      >
                        <div class="ep">
                          {{ Number(item.rating).toFixed(1) }} / 10
                        </div>
                        <div class="view">
                          <i class="fa fa-eye"></i> {{ item.year }}
                        </div>
                      </div>
                      <div class="product__item__text">
                        <ul>
                          <li
                            v-for="genre in item.genres.slice(0, 2)"
                            :key="genre.id"
                          >
                            {{ genre }}
                          </li>
                        </ul>
                        <h5>
                          <a href="#">{{ item.movie_title }}</a>
                        </h5>
                      </div>
                    </div>
                  </router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { ref, onBeforeMount } from "vue";
import router from "@/router";
import UserApi from "../services/user.service";
export default {
  name: "AboutUs",
  components: {},
  setup() {
    const imdbBotMovies = ref([]);
    onBeforeMount(() => {
      // IMDB BOT 10 movies
      UserApi.getImdbBotMovies(6)
        .then((response) => {
          imdbBotMovies.value = response.data.data;
        })
        .catch((error) => {
          console.log("error");
          console.log(error);
          console.log("error");
          showErroeMessage();
        });
    });

    const showErroeMessage = () => {
      return message.error("Server is busy, try again later");
    };

    //show Result Page
    const showResultPage = () => {
      localStorage.setItem("resultResource", 5);
      router.push({
        name: "ResultDisplay",
        params: {
          isPopularorHighScore: 5,
        },
      });
    };
    return { imdbBotMovies, showResultPage };
  },
};
</script>

